/* Write your CSS here */
.container {
  height: 100vh;
}
.bottom-div {
  background-color: white; /*change it to black*/
}
.heading {
  color: white;
  font-size: 20px;
}

.prime-video-container {
  background-color: #000000;
  padding-bottom: 32px;
  min-height: 100vh;
}
.image {
  width: 100%;
}

.movies-container {
  width: 90%;
  margin: auto;
  margin-bottom: 32px;
}

@media screen and (min-width: 768px) {
  .movies-container {
    max-width: 1110px;
  }
}

.movies-heading {
  color: white;
  font-size: 20px;
  font-family: 'Roboto';
  font-weight: 600px;
  line-height: 1.3;
  margin-top: 32px;
  margin-bottom: 25px;
}

@media screen and (min-width: 768px) {
  .movies-container {
    font-size: 32px;
  }
}
